
/**
 * @imports
 */
import Reflex from '@web-native-js/reflex';
import Chtml from './Chtml.js';
import ENV from './ENV.js';

/**
 * @scoped-html
 */
if (ENV.ScopedHTML) {
    ENV.ScopedHTML.factory = (newNode, scopedHTML) => {
        var chtml = new Chtml(newNode, scopedHTML);
        Object.defineProperty(newNode, '.chtml', chtml);
        return newNode;
    };
}

/**
 * @trap
 */
ENV.Trap = Reflex;

/**
 * @exports
 */
export {
    Chtml as default,
    ENV,
}