
/**
 * @imports
 */
import Jsen from '@web-native-js/jsen';
import _isTypeObject from '@web-native-js/commons/js/isTypeObject.js';
import _isFunction from '@web-native-js/commons/js/isFunction.js';
import _isNumeric from '@web-native-js/commons/js/isNumeric.js';
import _unique from '@web-native-js/commons/arr/unique.js';
import _following from '@web-native-js/commons/arr/following.js';
import defineBundleElements from './defineBundleElements.js';
import defineImportElements from './defineImportElements.js';
import createBundleMatrix from './createBundleMatrix.js';
import parseNamespace from './parseNamespace.js';
import ENV from './ENV.js';

/**
 * ---------------------------
 * The Chtml class
 * ---------------------------
 */				

export default class Chtml {

	/**
	 * @inheritdoc
	 */
	constructor(el, parentScopedHTML) {
		Object.defineProperty(this, 'el', {value: el});
		Object.defineProperty(this, 'scopedHTML', {value: el['.scopedHTML']});
		Object.defineProperty(this, 'parentScopedHTML', {value: parentScopedHTML});
		const namespaceParse = parseNamespace(el.getAttribute(ENV.params.namespaceAttribute) || '');
		Object.defineProperty(this, 'namespace', {value: namespaceParse.namespace});
		if (ENV.Trap) {
			// Setup sync
			if (ENV.Trap.observe) {
				ENV.Trap.observe(this.el, ENV.params.syncPropertyName, (sync, _sync, e) => {
					if (namespaceParse.subnamespace) {
						if (_isTypeObject(sync) && sync) {
							// Mirror
							ENV.Trap.observe(sync, changes => {
								return this.sync(sync, namespaceParse.subnamespace, syncListCallback);
							}, {tags:['#chtml-sync', this]});
						}
						if (_isTypeObject(_sync) && _sync) {
							// Unmirror
							ENV.Trap.unobserve(_sync, null, null, {tags:['#chtml-sync', this]});
						}
						// Initial Sync...
						return this.sync(sync || {}, namespaceParse.subnamespace, syncListCallback);
					}
				});
				const syncListCallback = ENV.syncListCallback || function(nodeName, node, syncItem, isExistingNode = false) {
					if (arguments.length > 2) {
						return ENV.Trap.set(node, syncPropertyName, syncItem);
					} else {
						return ENV.Trap.deleteProperty(node, syncPropertyName);
					}
				};
			}
		}
	}
	
	/**
	 * Binds a (reactive) list context to the instance.
	 * Childnodes will be automatically created/removed per key.
	 *
	 * @param array 		srcModel
	 * @param string 		subnamespace
	 * @param function 		syncListCallback
	 *
	 * @return ENV.Trap.MutationEvent
	 */
	sync(srcModel, subnamespace, syncListCallback = null) {
		// --------------
		var nodeNamespaceArray = subnamespace.split('//');
		// Create a namespace hash...
		if (nodeNamespaceArray[0].indexOf('[') > -1) {
			nodeNamespaceArray[0] = '"' + nodeNamespaceArray[0].replace(/\[/g, '" + ').replace(/\]/g, ' + "') + '"';
		}
		var srcModelKeys = ENV.Trap.keys(srcModel);
		var currentNodeNames = ENV.Trap.keys(this.scopedHTML.store);
		// --------------
		var e = new ENV.Trap.MutationEvent(this.el, {type:'remodelling'});
		_unique(srcModelKeys.concat(currentNodeNames)).forEach(nodeName => {
			nodeName = _isNumeric(nodeName) ? parseInt(nodeName) : nodeName;
			var existingNode = this.scopedHTML.find(nodeName);
			var rspns;
			if (ENV.Trap.has(srcModel, nodeName)) {
				var srcItem = ENV.Trap.get(srcModel, nodeName), isNewNode = false;
				if (!existingNode) {
					// --------------
					var nodeNamespaceArrayCopy = nodeNamespaceArray.slice();
					if (nodeNamespaceArrayCopy[0].indexOf('"') > -1) {
						nodeNamespaceArrayCopy[0] = Jsen.parse(nodeNamespaceArrayCopy[0]).eval(srcItem);
					}
					nodeNamespaceArrayCopy[0] += '/' + nodeName;
					var nodeEl = Chtml.import(nodeNamespaceArrayCopy.join('//'));
					// --------------
					if (nodeEl) {
						var following = _following(srcModelKeys, nodeName + ''/*numeric nodeName needs this*/, true/*length*/)
							.reduce((closest, _nodeName) => closest || this.scopedHTML.find(_nodeName), null);
						if (following) {
							following.before(nodeEl);
						} else {
							this.el.append(nodeEl);
						}
						existingNode = this.scopedHTML.add(nodeName, nodeEl);
						isNewNode = true;
					}
				}
				if (existingNode) {
					if (_isFunction(syncListCallback)) {
						rspns = syncListCallback(nodeName, existingNode, srcItem, isNewNode);
					}
				}
			} else if (existingNode) {
				if (_isFunction(syncListCallback)) {
					rspns = syncListCallback(nodeName, existingNode);
				}
				var remove = () => {
					existingNode.remove();
				};
				if (rspns instanceof Promise) {
					rspns.then(remove).catch(remove);
				} else {
					remove();
				}
			}
			e.response(rspns);
		});
		return e;
	}
	
	/**
	 * -------------------
	 * INSTANCE-RELATED METHODS
	 * -------------------
	 */
	
	/**
	 * The "init" function.
	 * Gives CHTML a global window context
	 * and lets it perform other necessary initializations.
	 *
	 * @param array		bundles
	 *
	 * @return void
	 */
	static init(bundles = []) {
		// ------------------
		// Chtml.contentLoadedPromise
		// ------------------
		Chtml.contentLoadedPromise = new Promise(resolve => {
			if (ENV.Window.document.readyState === 'complete') {
				resolve(); return;
			}
			ENV.Window.document.addEventListener('DOMContentLoaded', resolve, false);
			ENV.Window.addEventListener('load', resolve, false);
		});

		// ------------------
		// ENV.bundles
		// Chtml.loadingBundlesPromise
		// ------------------
		defineBundleElements();
		defineImportElements(Chtml, new Promise((resolveAutoImports, rejectAutoImports) => {
			Chtml.contentLoadedPromise.then(() => {
				if (_isFunction(bundles)) {
					bundles = bundles();
				}
				Chtml.bundleMatrix = createBundleMatrix(bundles);
				Chtml.bundleMatrix.loadingSources.then(() => {
					// Must come before resolveAutoImports()
					// so that resolveAutoImports calls won't throw errors
					Chtml.importsReady = true;
					resolveAutoImports();
				});	
			});
		}));
	}
	
	/**
	 * The "ready" function.
	 * Calls us when it becomes safe to run bundle-related code.
	 *
	 * @param function			callback
	 * @param bool				waitForBundles
	 *
	 * @return void
	 */
	static ready(callback, waitForBundles = true) {
		Chtml.contentLoadedPromise.then(() => {
			if (!waitForBundles) {
				callback(); return;
			}
			Chtml.bundleMatrix.loadingSources.then(callback);
		});
	}
	
	/**
	 * Imports a module from bundles.
	 *
	 * @param string						namespace
	 *
	 * @return HTMLElement
	 */
	static import(namespace) {
		if (Chtml.bundleMatrix) {
			// ------------------
			// Is someone trying to import while bundles are still loading?
			if (!Chtml.importsReady && !Chtml.warnedEarlyBundleAccess) {
				Chtml.warnedEarlyBundleAccess = true;
				console.warn('Remote bundles are still loading at this time! You should probabbly wrap bundle-dependent code within Chtml.ready(callback[, true/*waitForBundles*/]).');
			}
			return Chtml.bundleMatrix.find(namespace);
		}
	}
};
