
/**
 * @imports
 */
import _arrFrom from '@web-native-js/commons/arr/from.js';
import Chtml, {ENV} from './index.js';

/**
 * Configure CHTM with
 * a global window.
 */
ENV.Window = window;
Chtml.init(() => {
	var bundles = _arrFrom(window.document.querySelectorAll('[is="' + ENV.params.bundleAttribute + '"]')).reverse();
	return bundles.map(b => {
		if (b.hasAttribute('src') && !b.content.children.length) {
			return new Promise(resolve => {
				b.addEventListener('bundleloadsuccess', () => resolve(b));
				b.addEventListener('bundleloaderror', () => resolve(b));
			});
		}
		return b;
	})
});

if (!window.WebNative) {
    window.WebNative = {};
}
window.WebNative.Chtml = Chtml;
window.WebNative.Chtml.ENV = ENV;
